.TestTemplateA{
    position: absolute;
    left: 0;
    top: 0;
    height: 700px;

    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
}
.TTA_quest {
    width: 1200px; 
    text-align: center;
    word-wrap: normal;
    font: 40px "Times New Roman";    
}
.TTA_list {
    list-style-type: none;
    width: 1000px;   
    height: 400px;   
    margin-left: 50px;

    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
}
.TTA_list li{
    font: 30px "Times New Roman";
 
}
