 .TestTemplateB_2 {
    position: absolute;
    width: 1200px;
    height: 700px;

    left: 50%;
    top: 50%;
    margin-left: -600px;
    margin-top: -350px;
}

.TTB2_quest {
    position: absolute;
    width: 100%;
    top: 0px;
    left: 0;
    text-align: center;
    font: 30px "Times New Roman";
    border-bottom:  1px solid black;
    border-top:  1px solid black;
}
.TTB2_LBlock, .TTB2_RBlock {
    position: absolute;
    width: 600px;
    top: 200px;
    height: 400px;
    left: 0px;
    font: 25px "Times New Roman";

    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center; 
}
.TTB2_RBlock {
    left: 600px;
}
.TTB2_input {
    position: absolute;
    width: 300px;
    left: 450px;
    top: 600px;
    font: 30px "Times New Roman";
    text-align: center;
    text-transform: uppercase;
}
.TTB2_LBlock, .TTB2_RBlock>p {
    text-align: center;
}
